.navigation-menu{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.navigation-menu li{
    margin: 0px 20px;
    position: relative;
    right: 150%;
}
.navigation-menu li a {
    text-decoration: none;
    color: white;
}
.mobile-navigation li{
    margin-top: 20px;
}
.mobile-navigation li a {
    text-decoration: none;
    color: black;
    font-size: larger;
}
.active{
    color: goldenrod !important;
    font-weight: bold !important;
}