.home{
    height: 90vh;
    display:flex;
    justify-content: center;
    align-items:left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}
.headerContainer{
    width:auto;
    margin-left: 30px;
}
.home .headerContainer h1{
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 70px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
}
.home .headerContainer p{
    margin-top: 15px;
    padding: 0%;
    font-size: 40px;
    font-weight: lighter;
    color: white;

}
.home .headerContainer button{
    background-color: black;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    height:50px;
    width: 190px;
    text-decoration:none;
    color: white;
    font-size: 20px;
}
.headerContainer button:hover{
    background-color: grey;
    color: black;
    cursor: pointer;
    transition: 0.3s ease-in;
}
@media only screen and (max-width:600px){
    .home{
        justify-content: center;
        align-items: center;
    }
    .headerContainer{
        margin-left: 0%;
        border-radius: 10px;
        width: 95%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #121619;
        color: white;
    }
    .home .headerContainer h1{
        height: 30px;
        margin: 30px 0px;
        font-size: 40px;
        color: white;
    }
    .home .headerContainer p{
        font-size: 30px;    
    }
    .home .headerContainer button{
        background-color: white;
        margin-bottom: 30px;
        color: black;
    }
}
.glow-title {
    font-size: 4rem;
    font-weight: bold;
    color: #fff;
    text-shadow: 
      0 0 5px #ffd700,
      0 0 10px #ffd700,
      0 0 15px #ffd700,
      0 0 20px #ff9933,
      0 0 25px #ff9933,
      0 0 30px #ff9933,
      0 0 35px #ff9933;
    margin-bottom: 1rem;
  }
  